@import "src/common/constants/style";

.wrapper {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;

  .form {
    width: 350px;
    border-radius: 5px;
    background-color: #e3e5e7;
    padding: 30px;
  }

  .title {
    color: black;
    text-align: center;
    font-weight: 700;
    font-size: 20px;
  }

  .contentBlock {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    font-weight: 500;
  }

  .input {
    &:focus-visible {
      outline-color: #0079bf;
    }
  }

  .submitButton {
    color: white;
    width: 100%;
    margin-top: 10px;
    padding: 7px 15px;
    background-color: #0079bf;
    border: none;
    border-radius: 3px;
    transition: 75ms;

    &:hover {
      background-color: #026aa7;
    }
  }

  .loginWrapper {
    display: flex;
    gap: 3px;
    margin-top: 10px;

    .linkTitle {
      color: grey;
    }

    .link {
      color: blue;
      text-decoration: underline;
    }
  }

  .error {
    @extend %formErrorMessage;
  }
}
